import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatePipe } from '@angular/common';
import { LienHeQuaEmailService } from './lien-he-qua-email.service';
import { MatStepper } from '@angular/material/stepper';
import { LienHeEmailModel } from 'src/app/shared/models/lien-he-qua-email.model';
@Component({
  selector: 'app-lien-he-qua-email',
  templateUrl: './lien-he-qua-email.component.html',
  styleUrls: ['./lien-he-qua-email.component.scss'],
})
export class LienHeQuaEmailComponent implements OnInit {
  _yeuCau = new LienHeEmailModel();

  _hasValueStartDay = false;
  _hasValueEndDay = false;

  _minDate = new Date();
  _maxDate = new Date();
  campaignTwo: FormGroup;

  thongTinCoBanGroup: FormGroup;
  noiDungGroup: FormGroup;
  isEditable = true;
  showProgressBar = false;
  myFilter = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    // Prevent Saturday and Sunday from being selected.
    return day !== 0 && day !== 6;
  };
  @ViewChild(MatStepper) stepper: MatStepper;
  constructor(
    private _formBuilder: FormBuilder,
    public datepipe: DatePipe,
    private __service: LienHeQuaEmailService,

    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar
  ) {}
  ngOnInit(): void {
    this.thongTinCoBanGroup = this._formBuilder.group({
      hoTen: ['', Validators.required],
      email: ['', Validators.email],
    });
    this.noiDungGroup = this._formBuilder.group({
      message: ['', Validators.required],
    });
  }

  confirm() {
    this.showProgressBar = true;
    this._yeuCau.Name = this.thongTinCoBanGroup.value.hoTen;
    this._yeuCau.Email = this.thongTinCoBanGroup.value.email;
    this._yeuCau.Message = this.noiDungGroup.value.message;

    this.__service.add(this._yeuCau).subscribe(
      (data) => {
        this.showProgressBar = false;
        let res = this._yeuCau;
        this.stepper.reset();
        if (data === 'true') {
          this.__snackbar.open('Gửi thông tin thành công', null, {
            duration: 5000,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
        // this.dialogMainRef.close(
        //   {
        //     data: res,
        //     result: true
        //   }
        // )
      },
      (error) => {
        //Khả năng là do trùng mã (không hiển thị)
        this.showProgressBar = false;
        this._alertDialog.open(AlertDialogComponent, {
          disableClose: true,
          width: '17rem',
          data: {
            dataKey: 'Có lỗi xảy ra',
          },
        });
      }
    );
  }
}
