import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../environments/environment';
import { LienHeEmailModel } from '../../../../shared/models/lien-he-qua-email.model';
@Injectable({providedIn: 'root'})
export class LienHeQuaEmailService {
    constructor(private httpClient: HttpClient) {}
   
      add(yeuCau: LienHeEmailModel): Observable<any> {
        let url = environment.api.url + 'LienHeQuaEmail/addAnonymous'
        return this.httpClient.post(url, yeuCau)
      }
}